<template>
	<view class='wrapper'>
		<block v-if="orderInfo.orderList.length>0">
			<view class="switch">
				<scroll-view
					:scroll-x="true" 
					:scroll-with-animation="true" 
					:enable-back-to-top="true"
					:scroll-into-view="domIndex"
				>
				<view class='list' :style="[{width:orderInfo.orderList.length*150+'px'}]">
					<view 
						:class="[idx==index?'active':'','item']" 
						v-for="(item,index) in orderInfo.orderList" 
						:key="index" 
						@click="changeSlide(item,index)"
						:id="'dom'+index"
					>
						<view class="want">意向车型</view>
						<view class="modal">{{item.carTypeName}}</view>
					</view>
				</view>
				</scroll-view>
			</view>
			<view class="cu-timeline">
				<view 
					:class="['cu-item',currentStep>=item.step?'text-qxblue':'no_pass',currentStep==item.step?'active':'']"
					v-for="(item,index) in progressList"
					:key="index"
				>
					<view class="look_car time_item">
						<view class="title flex-row-start">
							<text>{{item.name}}</text>
							<text class="status" v-if="currentOrderInfo.orderState==-1 && currentStep==item.step">{{currentOrderInfo.orderStateName}}</text>
						</view>
						<!-- 普通节点 -->
						<block v-if="item.step<6">
							<view class='date' v-if='currentStep==item.step && currentOrderInfo.orderState!=-1'>
								<picker mode="date" :value="lookDate" :start="startTime"  :end="endTime" @change="dataChange($event,item)">
									<view class="picker">
										请选择日期
									</view>
								</picker>
							</view>
							<view v-else class="select_date">{{item.nodeDate!=-1?item.nodeDate:" "}}</view>
						</block>
						
						<block v-else>
							<view class="upload_w" v-if="currentStep>=item.step">
								<view class="select_date" v-if="item.nodeDate">{{item.nodeDate!=-1?item.nodeDate:" "}}</view>
								<view class="upload">
									<view class="item" v-for='(litt,idx) in affixList' :key='idx'>
										<img src="../../static/delete.png" class='delete' @click='deleteFile(litt)' v-if="currentStep==item.step">
										<view @click='lookAffix(litt)' class='inner_w'>
											<image :src="litt.openUrl" class='pic'/>
										</view>
									</view>
									<view class="inner item" @click='takePicture' v-if="affixList.length<1 && currentStep==item.step && currentOrderInfo.orderState!=-1">
										<image src="../../static/pic.png"></image>
									</view>
								</view>
								<view class="make_sure" v-if="currentStep==item.step" @click="uploadAffix(item)">
									<button :class='["cu-btn","block","text-qxblue",affixBtnDisabled?"disabled":""]' :disabled="affixBtnDisabled">确认上传</button>
								</view>
							</view>
						</block>
					</view>
				</view>
				
				<!-- 评价 -->
				<view
					:class="['cu-item',currentStep>=totalStep?'text-qxblue':'no_pass',currentStep==totalStep?'active':'']"
				>
					<view class="time_item">
						<view class="title flex-row-start">
							<text>{{currentStep>totalStep?"已评价":"服务评分"}}</text>
							<text class="status" v-if="currentOrderInfo.orderState==-1 && currentStep>=totalStep">{{currentOrderInfo.orderStateName}}</text>
						</view>
						<view class="sd" v-if="currentStep>=totalStep && currentOrderInfo.orderState!=-1">
							<view class="rate_w">
								<view class="rate_item" v-for="(item,index) in rateList" :key="index">
									<Evaluate  :size="36" class="rate" :margin="'0 15'" :value="item.value" :text="item.name" :disabled="true"/>
								</view>
							</view>
						</view>
					</view>
				</view>
				
			</view>
		</block>
		
		
		<block v-else>
			<view class="no_data">
				暂无数据，请联系员工为您添加购车意向
			</view>
		</block>
		<!-- 图片查看 -->
		<view class="cu-modal" :class="lookImg?'show':''" catchtouchmove="return">
			<view class="cu-dialog">
				<view class="bg-img">
					<image :src="currentImgUrl" class="dialogImg" mode="widthFix"></image>
				</view>
				<view class="cu-bar bg-white">
					<view class="action margin-0 flex-sub" @click="lookImg=false">关闭</view>
				</view>
			</view>
		</view>
		
		<!-- 评价 弹框-->
		<view class="cu-modal rate_dialog" :class="rateDialog?'show':''" catchtouchmove="return">
			<view class="cu-dialog padding">
				<view class="content text-left">
					<view class="title">服务评价</view>
					<view class="tip text-qxblue">需完成评价再确认上传</view>
					<view class="rate_w">
						<view class="rate_item" v-for="(item,index) in rateList" :key="index">
							<Evaluate 
								@change="startChange($event,item)" 
								:value="item.value" 
								:size="36" 
								class="rate" 
								:margin="'0 15'"
								:text="item.name"
							/>
						</view>
					</view>
				</view>
				<view class="action margin-0 flex-sub" @click="makeScore(item)">
					<button class="cu-btn make_sure">确认评分并上传</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {http2Https,dateToms,formatTime} from '../../common/tools.js'
	import Evaluate from '@/components/evaluate/rate.vue'
	export default {
		data() {
			return {
				domIndex:'dom0',
				idx:0, //当前查询的订单
				currentStep:null, //默认第一步
				totalStep:undefined, //总步数
				rateDialog:false,
				affixBtnDisabled:true,
				affixList:[], //文件列表
				startTime:"2020-05-20",//  限制日期
				endTime:formatTime(new Date().getTime(),true),
				userId:'',
				orderInfo:{ //订单数据
					orderList:[]
				},
				rate:false,
				orderFlag:true,
				date:'', // 临时date
				progressList:[],
				controlParams:{
					orderId:'',//订单号	Long	是	
					code:'',//进度节点编码	Integer	是	
					nodeDate:'',//进度节点日期	Long	是	当code=25时，该项系统时间即可；
					attFileUrls:''
				},
				lookImg:false,
				currentImgUrl:null,
				currentOrderId:null,
				rateList:[{name:'4S店满意度',value:0},{name:'员工满意度',value:0},{name:'员工专业度',value:0}],
				canRate:false,
				orderStatus:"",//订单进度 -1时是取消
				currentOrderInfo:{}
			};
		},
		methods:{
			changeSlide(item,index){
				this.idx=index
				this.affixList=[]
				this.startTime="2020-05-20" //重置时间后，自己在计算去
				this.currentOrderId=item.orderId
				this.rateList=[{name:'4S店满意度',value:0},{name:'员工满意度',value:0},{name:'员工专业度',value:0}]
				this.getOrderProgress(item.orderId)
				// 查询订单状态
				this.currentOrderInfo=item
			},
			dataChange(e,nodeInfo){
				nodeInfo.nodeDate=e.detail.value
				this.controlParams.nodeDate=dateToms(nodeInfo.nodeDate)
				this.controlParams.code=nodeInfo.code
				this.controlProgress(nodeInfo)
			},
			startChange(e,item){
				item.value=e.value
			},
			uploadAffix(item){ //上传附件
				uni.showModal({
					title: '提示',
					content: `上传后不支持修改，是否确认上传?`,
					success: (res)=>{
						if (res.confirm) {
							let _picList=[]
							for(var i=0;i<this.affixList.length;i++){
								_picList.push(this.affixList[i].openUrl)
							}
							let _attFileUrls=String(_picList)
							this.controlParams.code=item.code
							this.controlParams.attFileUrls=_attFileUrls
							this.controlProgress(item)
						}
					}
				})
			},
			makeScore(item){
				for(var i=0;i<this.rateList.length;i++){
					if(!this.rateList[i].value){ uni.showToast({title: `请对${this.rateList[i].name}进行评价`,icon:'none'});return}
				}
				var _params={
					orderId:this.currentOrderId,
					score1:this.rateList[0].value,
					score2:this.rateList[1].value,
					score3:this.rateList[2].value
				}
				this.request("/order/member/evaluate",_params).then((res)=>{
					if(res.resultCode==0){
						this.currentStep++
						this.rateDialog=false
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			deleteFile(item){
				this.request("/common/deleteFile",{fileId:item.fileId}).then((res)=>{
					if(res.resultCode==0){
						var _arr=this.affixList
						for(let i=0;i<_arr.length;i++){ //删除 所有附件中的文件
							if(item.fileId==_arr[i].fileId){
								this.affixList.splice(i,1)
								break
							}
						}
						if(this.affixList.length<=0){
							this.affixBtnDisabled=true
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			takePicture(){
				var _that=this
				uni.chooseImage({
					count:1,
					sizeType:["compressed"],
					sourceType: ['album', 'camera'], //从相册选择
					success: function (res) {
						let imgPaths=res.tempFilePaths
						for(let i=0;i<imgPaths.length;i++){
							(function(i){
								uni.showLoading()
								uni.uploadFile({
									url:getApp().globalData.requestUrl+"/common/upload",
									filePath:imgPaths[i],
									name: 'upFile',
									header: {
										'content-type': 'multipart/form-data',
										'service-code':'QDMS',
										"ticket":JSON.parse(uni.getStorageSync('token')).value
									},
									success:(res)=>{
										const _data=JSON.parse(res.data)
										let result=_data.data
										_that.affixList.push({
											openUrl:http2Https(result.openUrl),
											fileId:result.fileId
										})
										_that.affixBtnDisabled=false
									},
									complete:(res)=>{
										uni.hideLoading()
									},
								})
							})(i)
						}
					}
				});
			},
			lookAffix(item){
				if(this.mobileModel=="VOG-AL00"){ //华为p30
					this.currentImgUrl=item.openUrl;
					this.lookImg=true
				}else{
					//获取 url数组
					var _urlList=[]
					this.affixList.forEach((item)=>{
						_urlList.push(item.openUrl)
					})
					uni.showLoading()
					uni.previewImage({
						current:item.openUrl,
						urls: _urlList,
						longPressActions:{
							itemList: ['发送给朋友', '保存图片', '收藏']
						},
						fail(err){
							this.currentImgUrl=item.openUrl;
							this.lookImg=true
						},
						complete(){
							uni.hideLoading()
						}
					})
				}
			},
			getOrders(){ // 获取所有订单
				this.request("/order/member/orders",{memberId:this.userId}).then((res)=>{
					if(res.resultCode==0){
						this.orderInfo=res.data
						if(this.orderInfo.orderList.length>0){
							this.getOrderProgress(this.currentOrderInfo.orderId)
							this.currentOrderId=this.currentOrderInfo.orderId
							this.orderInfo.orderList.forEach((item,index)=>{
								if(item.orderId==this.currentOrderId){
									this.idx=index
									this.domIndex="dom"+this.idx
								}
							})
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			getOrderProgress(orderId){ //根据订单获取进度
				this.request("/order/progress/show",{orderId:orderId}).then((res)=>{
				 	this.controlParams.orderId=orderId
					if(res.resultCode==0){
						let step=1
						// 判断节点处于第一步
						this.progressList=res.data
						this.totalStep=this.progressList.length+1 //777
						this.progressList.forEach((item,index)=>{
							item.step=index+1; // 赋予步数
							if(item.nodeDate!=-1){
								item.nodeDate=formatTime(item.nodeDate,true)
								this.startTime=item.nodeDate
								step++
							}
							if(item.attFileUrls){
								let _imgList=item.attFileUrls.split(',')
								for(var i=0;i<_imgList.length;i++){
									this.affixList.push({
										openUrl:_imgList[i],
										fileId:i
									})
								}
							}
						})
						
						this.currentStep=step
						// console.log(this.currentStep,this.totalStep)
						if(this.currentStep>=this.totalStep){ //最后一步才获取得分情况
							this.getScore(this.currentOrderId)
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			controlProgress(nodeInfo){ // 操作进度
				this.request("/order/progress/treat",this.controlParams).then((res)=>{
					if(res.resultCode==0){
						nodeInfo.name="已"+nodeInfo.name
						if(nodeInfo.step<6){
							nodeInfo.nodeDate=formatTime(this.controlParams.nodeDate,true)
							this.startTime=formatTime(this.controlParams.nodeDate,true)
						}else{ //上传附件时间
							nodeInfo.nodeDate=formatTime(res.data,true)
						}
						this.currentStep++
						if(this.currentStep>=7 && this.currentOrderInfo.orderState!=-1){ //到评价这步并且订单没取消
							this.rateDialog=true
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			getScore(orderId){ // 获取评分
				this.request("/order/evaluate/list",{orderId:this.currentOrderId}).then((res)=>{
					var _data=res.data
					if(res.resultCode==0){
						if(_data.length>0){ //如果评价了
							this.rateList[0].value=_data[0].score
							this.rateList[1].value=_data[1].score
							this.rateList[2].value=_data[2].score
							this.currentStep++
						}else{ //用户去评价
							if(this.currentOrderInfo.orderState!=-1){
								this.rateDialog=true
							}
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			}
		},
		onLoad(params){
			this.userId=params.userId
			this.currentOrderInfo=JSON.parse(params.orderInfo)
			// console.log("当前订单",this.currentOrderInfo)
			this.getOrders()
		},
		components:{
			Evaluate
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
	}
</script>

